/* ============================================
   ðŸŽ¨ PROMINENT EXPERIENCE CARDS (OPTIMIZED)
   ============================================ */

.experience-card {
  width: 100%;
  margin-bottom: 0;
  border-radius: 16px;
  display: flex;
  flex-direction: row;

  /* ðŸš€ OPTIMIZATION: Hardware Acceleration & Stacking Context */
  transform: translateZ(0); 
  backface-visibility: hidden;
  
  /* ðŸš€ LAZY RENDERING: The Game Changer 
     This tells the browser to skip rendering cards currently off-screen.
     'contain-intrinsic-size' prevents scrollbar jumping. Adjust 200px to approx card height. */
  content-visibility: auto; 
  contain-intrinsic-size: 0 200px; 
  contain: layout paint style;

  /* Enhanced Glassmorphism */
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(15px) saturate(180%);
  -webkit-backdrop-filter: blur(15px) saturate(180%);
  border: 1px solid rgba(255, 255, 255, 0.3);

  /* ðŸš€ OPTIMIZATION: Specific Transitions 
     Never use 'all'. It forces the browser to calculate every property. */
  transition: transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275),
              background 0.4s ease,
              border-color 0.4s ease,
              box-shadow 0.4s ease;

  overflow: hidden;
  position: relative;

  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08),
    inset 0 1px 0 rgba(255, 255, 255, 0.6);
}

/* Dark mode */
.dark-mode .experience-card {
  background: rgba(29, 29, 29, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.05);
}

/* ============================================
   ðŸŽ¨ SPOTLIGHT EFFECT (Optimized)
   ============================================ */
.experience-card::before {
  content: "";
  position: absolute;
  inset: 0;
  /* ðŸš€ OPTIMIZATION: Will-change only on the specific interaction layer */
  will-change: opacity; 
  background: radial-gradient(circle 300px at var(--mouse-x, 50%) var(--mouse-y, 50%),
      rgba(41, 98, 255, 0.12) 0%,
      transparent 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  z-index: 1;
}

.experience-card:hover::before {
  opacity: 1;
}

/* ============================================
   ðŸŽ¨ HOVER EFFECTS
   ============================================ */
.experience-card:hover {
  transform: translateY(-8px) scale(1.02);
  background: rgba(255, 255, 255, 0.85);
  border-color: rgba(41, 98, 255, 0.4);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.15), 0 0 0 1px rgba(41, 98, 255, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.8);
}

.dark-mode .experience-card:hover {
  background: rgba(29, 29, 29, 0.95);
  border-color: rgba(41, 98, 255, 0.5);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.5), 0 0 0 1px rgba(41, 98, 255, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

/* Gradient overlay */
.experience-card::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg,
      rgba(41, 98, 255, 0.08),
      rgba(130, 177, 255, 0.08));
  opacity: 0;
  pointer-events: none; /* Prevent interference */
  transition: opacity 0.3s ease;
}

.experience-card:hover::after {
  opacity: 1;
}

/* ============================================
   ðŸŽ¨ LOGO SECTION (Anti-Layout Shift)
   ============================================ */
.experience-card-logo-div {
  padding: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.1);
  /* ðŸš€ OPTIMIZATION: Reduce blur calculation area */
  backdrop-filter: blur(10px); 
  border-right: 1px solid rgba(211, 211, 211, 0.2);
  min-width: 140px;
  position: relative;
  z-index: 2;
  transition: transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275), 
              background 0.4s ease, 
              border-color 0.4s ease;
}

.experience-card:hover .experience-card-logo-div {
  background: rgba(41, 98, 255, 0.1);
  border-right-color: rgba(41, 98, 255, 0.3);
  transform: scale(1.05);
}

.experience-card-logo {
  width: 90px;
  height: 90px;
  /* ðŸš€ OPTIMIZATION: Aspect Ratio prevents Layout Shift (CLS) during loading */
  aspect-ratio: 1 / 1; 
  object-fit: contain;
  transition: transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275), 
              filter 0.4s ease, 
              box-shadow 0.4s ease;
  filter: grayscale(40%) brightness(0.9);
  border-radius: 8px;
  /* ðŸš€ OPTIMIZATION: GPU promote only on hover interaction usually, 
     but since this scales/rotates, we keep it simple without 'will-change' 
     unless animation stutters. */
}

.experience-card:hover .experience-card-logo {
  transform: scale(1.2) rotate(5deg);
  filter: grayscale(0%) brightness(1.1) saturate(1.2);
  box-shadow: 0 8px 20px rgba(41, 98, 255, 0.3);
}

/* ============================================
   ðŸŽ¨ BODY SECTION
   ============================================ */
.experience-card-body-div {
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 2;
}

.experience-card-header-div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 1.5rem;
  align-items: flex-start;
}

/* ============================================
   ðŸŽ¨ TEXT ELEMENTS
   ============================================ */
.experience-card-title {
  font-family: "Google Sans Medium", sans-serif;
  font-size: 1.5rem;
  margin: 0 0 0.5rem 0;
  /* ðŸš€ OPTIMIZATION: Specific transition */
  transition: color 0.4s ease, transform 0.4s ease;
  position: relative;
  display: inline-block;
  color: #1f2937;
}

.dark-mode .experience-card-title {
  color: #f9fafb;
}

.experience-card:hover .experience-card-title {
  color: #2962ff;
  transform: translateX(4px) translateY(-2px);
}

/* Underline Animation */
.experience-card-title::after {
  content: "";
  position: absolute;
  bottom: -6px;
  left: 0;
  width: 60px;
  height: 3px;
  background: linear-gradient(90deg, #2962ff, #82b1ff, #2962ff);
  background-size: 200% 100%;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  border-radius: 2px;
  /* ðŸš€ OPTIMIZATION: Only animate transform on GPU */
  will-change: transform;
}

.experience-card:hover .experience-card-title::after {
  transform: scaleX(1);
  animation: gradientMove 3s ease-in-out infinite;
}

@keyframes gradientMove {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.experience-card-company {
  font-family: "Google Sans Regular", sans-serif;
  font-size: 1.2rem;
  margin: 0;
  display: flex;
  align-items: center;
}

.experience-card-company>a {
  position: relative;
  color: #6b7280;
  text-decoration: none;
  transition: color 0.4s ease, transform 0.4s ease;
  display: inline-flex;
  align-items: center;
  font-weight: 500;
}

.dark-mode .experience-card-company>a {
  color: #9ca3af;
}

.experience-card:hover .experience-card-company>a {
  color: #2962ff;
  transform: translateX(2px);
}

.experience-card-company>a:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -3px;
  left: 0;
  background: linear-gradient(90deg, #2962ff, #82b1ff);
  visibility: hidden;
  transform: scaleX(0);
  transition: transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  border-radius: 1px;
}

.experience-card:hover .experience-card-company>a:before {
  visibility: visible;
  transform: scaleX(1);
}

/* ============================================
   ðŸŽ¨ DURATION & LOCATION
   ============================================ */
.experience-card-duration,
.experience-card-location {
  font-family: "Google Sans Regular", sans-serif; /* Consolidate fonts if possible */
  font-size: 1.1rem;
  margin: 0;
  color: #6b7280;
  display: flex;
  align-items: center;
  transition: color 0.4s ease, transform 0.4s ease;
}

.experience-card-duration { margin-bottom: 0.5rem; font-family: "Google Sans Medium", sans-serif; }

.dark-mode .experience-card-duration,
.dark-mode .experience-card-location {
  color: #9ca3af;
}

.experience-card-duration::before,
.experience-card-location::before {
  margin-right: 8px;
  font-size: 1em;
  transition: transform 0.3s ease;
  display: inline-block; /* Required for transform to work */
}

.experience-card-duration::before { content: "â±"; }
.experience-card-location::before { content: "ðŸ“"; }

.experience-card:hover .experience-card-duration,
.experience-card:hover .experience-card-location {
  color: #2962ff;
  transform: translateX(2px);
}

.experience-card:hover .experience-card-duration::before { transform: rotate(15deg) scale(1.1); }
.experience-card:hover .experience-card-location::before { transform: rotate(-10deg) scale(1.1); }

/* ============================================
   ðŸŽ¨ DESCRIPTION
   ============================================ */
.experience-card-description {
  font-family: "Google Sans Regular", sans-serif;
  font-size: 1.1rem;
  line-height: 1.7;
  margin: 1.5rem 0 0 0;
  color: rgba(0, 0, 0, 0.7);
  position: relative;
  padding-left: 20px;
  transition: color 0.4s ease, transform 0.4s ease;
}

.dark-mode .experience-card-description {
  color: rgba(255, 255, 255, 0.7);
}

.experience-card:hover .experience-card-description {
  color: rgba(0, 0, 0, 0.9);
  transform: translateY(-2px);
}

.dark-mode .experience-card:hover .experience-card-description {
  color: rgba(255, 255, 255, 0.9);
}

.experience-card-description::before {
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: linear-gradient(135deg, #2962ff, #82b1ff);
  opacity: 0.8;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.experience-card:hover .experience-card-description::before {
  transform: scale(1.5);
  box-shadow: 0 0 12px rgba(41, 98, 255, 0.6);
}

/* ============================================
   ðŸŽ¨ ENTRANCE ANIMATIONS
   ============================================ */
/* ðŸš€ OPTIMIZATION: Only use animation if user prefers motion */
@media (prefers-reduced-motion: no-preference) {
  .experience-card {
    animation: experienceCardEntrance 0.6s cubic-bezier(0.34, 1.56, 0.64, 1) backwards;
  }
}

@keyframes experienceCardEntrance {
  from {
    opacity: 0;
    transform: translateY(30px) scale(0.95);
    /* ðŸš€ OPTIMIZATION: Removed blur from animation. 
       Animating blur is VERY expensive on mobile. 
       Opacity+Transform is enough for a smooth feel. */
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Staggered entrance */
.experience-card:nth-child(1) { animation-delay: 0.1s; }
.experience-card:nth-child(2) { animation-delay: 0.2s; }
.experience-card:nth-child(3) { animation-delay: 0.3s; }
.experience-card:nth-child(4) { animation-delay: 0.4s; }
.experience-card:nth-child(5) { animation-delay: 0.5s; }

/* ============================================
   ðŸŽ¨ RESPONSIVE DESIGN
   ============================================ */
@media (max-width: 768px) {
  .experience-card {
    margin-bottom: 20px;
    flex-direction: column;
    text-align: center;
    padding: 20px;
    /* ðŸš€ OPTIMIZATION: Reset aspect ratio for vertical layout if needed */
    contain-intrinsic-size: 0 400px; 
  }

  /* ðŸš€ OPTIMIZATION: Disable heavy hover effects on touch devices */
  @media (hover: none) {
    .experience-card:hover {
      transform: none;
      box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08); /* Keep default shadow */
    }
  }

  .experience-card:hover {
    transform: translateY(-6px) scale(1.01);
  }

  .experience-card-logo-div {
    min-width: 100%;
    border-right: none;
    border-bottom: 1px solid rgba(211, 211, 211, 0.2);
    padding: 1.5rem;
  }
  
  /* Rest of responsive styles remain the same... */
  .experience-card-logo { width: 80px; height: 80px; margin: 0 auto 15px; }
  .experience-card-header-div { flex-direction: column; margin-bottom: 15px; }
  .experience-card-title { text-align: center; font-size: 1.3rem; margin-bottom: 8px; }
  .experience-card-company { justify-content: center; margin-bottom: 8px; }
  .experience-card-duration { justify-content: center; margin-top: 8px; }
  .experience-card-location { justify-content: center; margin-bottom: 15px; }
  .experience-card-description { text-align: left; }
}

@media (max-width: 480px) {
  .experience-card { padding: 15px; }
  .experience-card-logo { width: 70px; height: 70px; }
}

/* ============================================
   ðŸŽ¨ ACCESSIBILITY & REDUCED MOTION
   ============================================ */
@media (prefers-reduced-motion: reduce) {
  .experience-card,
  .experience-card * {
    animation: none !important;
    transition: none !important;
  }
}