/* ============================================
   ðŸš€ OPTIMIZED EXPERIENCE ACCORDION
   ============================================ */

/* Main container with Lazy Rendering */
.experience-accord {
  position: relative;
  padding: 40px 20px;
  max-width: 1200px;
  margin: 0 auto;

  /* ðŸš€ PERFORMANCE: Lazy load off-screen content */
  content-visibility: auto; 
  contain-intrinsic-size: 1000px; /* Prevents scrollbar jumping */
}

/* Floating background elements */
/* ðŸš€ OPTIMIZATION: Added will-change to hint browser */
.experience-accord::before,
.experience-accord::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  pointer-events: none;
  z-index: 0;
  will-change: transform, opacity;
}

.experience-accord::before {
  top: 10%;
  left: 5%;
  width: 350px;
  height: 350px;
  background: radial-gradient(circle at center, rgba(41, 98, 255, 0.12) 0%, transparent 70%);
  animation: floatExp 20s infinite ease-in-out;
}

.experience-accord::after {
  bottom: 10%;
  right: 5%;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle at center, rgba(255, 165, 0, 0.1) 0%, transparent 70%);
  animation: floatExp 25s infinite ease-in-out reverse;
}

@keyframes floatExp {
  0%, 100% { transform: translate(0, 0) scale(1); opacity: 0.6; }
  25% { transform: translate(40px, -40px) scale(1.1); opacity: 0.8; }
  50% { transform: translate(-30px, 50px) scale(0.95); opacity: 0.7; }
  75% { transform: translate(-50px, -30px) scale(1.05); opacity: 0.9; }
}

/* ============================================
   ðŸŽ¨ ENHANCED ACCORDION PANEL
   ============================================ */
.accord-panel {
  position: relative;
  margin-bottom: 30px;
  background: rgba(255, 255, 255, 0.05) !important;
  
  /* ðŸš€ PERFORMANCE: Blur is expensive, use cautiously */
  backdrop-filter: blur(20px) saturate(180%) brightness(1.1);
  -webkit-backdrop-filter: blur(20px) saturate(180%) brightness(1.1);
  
  border-radius: 20px !important;
  border: 1px solid rgba(255, 255, 255, 0.15) !important;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.12), 0 5px 15px rgba(0, 0, 0, 0.08);
  
  /* ðŸš€ PERFORMANCE: Specific transitions only */
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1), 
              box-shadow 0.5s ease, 
              background 0.5s ease;
  
  overflow: hidden;
  z-index: 1;
  transform: translateZ(0); /* Hardware Acceleration */
}

/* â¬‡ï¸ MINIMIZED GLASS STRIP (As Requested) â¬‡ï¸ */
/* Combined the previous heavy reflection into a subtle edge sheen */
.accord-panel::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%; /* Start completely off screen */
  width: 20%;  /* Much thinner */
  height: 100%;
  
  /* Very subtle gradient */
  background: linear-gradient(
    90deg,
    transparent 0%,
    rgba(255, 255, 255, 0.05) 50%, 
    transparent 100%
  );
  
  transform: skewX(-25deg); /* Angled slightly */
  pointer-events: none;
  z-index: 2;
  
  /* ðŸš€ PERFORMANCE: Slow, infinite loop that creates a "breathing" light effect */
  animation: glassSheen 15s infinite linear; 
}

@keyframes glassSheen {
  0% { left: -50%; opacity: 0; }
  50% { opacity: 0.5; }
  100% { left: 150%; opacity: 0; }
}

/* Top right corner accent */
.accord-panel::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 60px;
  border-top: 2px solid rgba(255, 255, 255, 0.2);
  border-right: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 0 20px 0 0;
  transition: width 0.5s ease, height 0.5s ease, border-color 0.5s ease;
  z-index: 2;
}

.accord-panel:hover::after {
  width: 80px;
  height: 80px;
  border-color: rgba(255, 255, 255, 0.4);
}

/* Enhanced hover effect */
.accord-panel:hover {
  transform: translateY(-5px) scale(1.01);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.18);
  background: rgba(255, 255, 255, 0.08) !important;
}

/* ============================================
   ðŸŽ¨ HEADER
   ============================================ */
.accord-panel-header {
  position: relative;
  padding: 20px 25px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.06), rgba(255, 255, 255, 0.02));
  backdrop-filter: blur(10px); /* Lower blur for header */
  border-radius: 20px;
  transition: padding 0.4s ease, background 0.4s ease;
  z-index: 3;
  border-left: 6px solid transparent;
  margin: 2px;
}

.accord-panel-header:hover {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.04));
  padding-left: 30px;
}

/* Header title styling */
.accord-panel-header h3 {
  margin: 0;
  font-size: 24px;
  font-weight: 600;
  font-family: "Google Sans Medium";
  transition: transform 0.3s ease;
  position: relative;
  z-index: 4;
}

.accord-panel-header:hover h3 {
  transform: translateX(5px);
}

/* Count badge styling */
.accord-panel-header h3 span {
  display: inline-block;
  padding: 3px 10px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.1);
  margin-left: 12px;
  font-size: 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.15);
  transition: transform 0.3s ease, background 0.3s ease;
}

.accord-panel-header:hover h3 span {
  background: rgba(255, 255, 255, 0.15);
  transform: scale(1.05);
}

/* Toggle button (+ / -) */
.accord-panel-header > span {
  position: relative;
  z-index: 4;
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  font-size: 24px;
  font-weight: bold;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.3s ease;
}

.accord-panel-header > span:hover {
  transform: scale(1.15) rotate(90deg);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
}

.accord-panel-header.active > span {
  transform: rotate(180deg);
}

/* ============================================
   ðŸŽ¨ CONTENT AREA
   ============================================ */
.accord-panel-content {
  position: relative;
  padding: 25px;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 0 0 18px 18px;
  margin-top: 5px;
  /* ðŸš€ PERFORMANCE: Optimize open animation */
  will-change: max-height, opacity, transform;
  animation: slideDown 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 3;
  overflow: hidden; /* Critical for slide animation */
}

@keyframes slideDown {
  from { opacity: 0; transform: translateY(-10px); max-height: 0; }
  to { opacity: 1; transform: translateY(0); max-height: 2000px; }
}

.accord-panel-content::before {
  content: "";
  position: absolute;
  top: 0;
  left: 25px;
  right: 25px;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2) 50%, transparent);
}

/* Section-specific color variants (Simplified shadows) */
.accord-panel[data-section="Work"] .accord-panel-header { border-left-color: #2962ff; }
.accord-panel[data-section="Work"]:hover { box-shadow: 0 25px 50px rgba(41, 98, 255, 0.15); }

.accord-panel[data-section="Internships"] .accord-panel-header { border-left-color: #ffa500; }
.accord-panel[data-section="Internships"]:hover { box-shadow: 0 25px 50px rgba(255, 165, 0, 0.15); }

.accord-panel[data-section="Volunteerships"] .accord-panel-header { border-left-color: #a7f432; }
.accord-panel[data-section="Volunteerships"]:hover { box-shadow: 0 25px 50px rgba(167, 244, 50, 0.15); }

/* Bottom left corner accent */
.accord-panel-corner {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 60px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  border-left: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 0 0 0 20px;
  transition: width 0.5s ease, height 0.5s ease, border-color 0.5s ease;
  z-index: 2;
  pointer-events: none;
}

.accord-panel:hover .accord-panel-corner {
  width: 80px;
  height: 80px;
  border-color: rgba(255, 255, 255, 0.4);
}

/* Removed old .accord-panel-light class entirely to reduce DOM nodes and painting */

.accord-panel-content > div { margin-bottom: 20px; }
.accord-panel-content > div:last-child { margin-bottom: 0; }

/* ============================================
   ðŸ“± RESPONSIVE & ACCESSIBILITY
   ============================================ */
@media (max-width: 1024px) {
  .experience-accord { padding: 30px 15px; }
  .accord-panel { margin-bottom: 25px; }
  .accord-panel-header h3 { font-size: 22px; }
}

@media (max-width: 768px) {
  /* ðŸš€ PERFORMANCE: Hide heavy background blobs on mobile */
  .experience-accord::before, .experience-accord::after { display: none; }
  
  .experience-accord { padding: 20px 10px; }
  .accord-panel { margin-bottom: 20px; }
  
  /* Disable hover transforms on touch */
  .accord-panel:hover { transform: none; }
  
  .accord-panel-header { padding: 15px 20px; }
  .accord-panel-header h3 { font-size: 20px; }
  
  .accord-panel-header h3 span { 
    display: block; 
    margin-left: 0; 
    margin-top: 5px; 
    width: fit-content; 
  }
  
  /* Remove animations on mobile for battery saving */
  .accord-panel::before { display: none; } 
}

@media (max-width: 480px) {
  .experience-accord { padding: 15px 5px; }
  .accord-panel-header { padding: 12px 15px; flex-wrap: wrap; }
  .accord-panel-header h3 { font-size: 18px; width: 100%; margin-bottom: 8px; }
  .accord-panel-header > span { position: absolute; top: 12px; right: 15px; width: 30px; height: 30px; font-size: 18px; }
  .accord-panel-content { padding: 12px; }
}

/* Accessibility: Reduced Motion */
@media (prefers-reduced-motion: reduce) {
  .experience-accord::before, .experience-accord::after, .accord-panel::before {
    animation: none !important;
    display: none;
  }
  .accord-panel, .accord-panel-header, .accord-panel-content {
    transition: none !important;
    animation: none !important;
  }
}